const React = {
    createElement: (tag, props, ...children) => {
        if (typeof tag === 'function') {
            return tag(props);
        }

        const element = { tag, props: { ...props, children } };
        return element;
    }
}

// Maps the virtual DOM to the real DOM
const render = (vdom, container) => {
    const renderedElement = document.createElement(vdom.tag);

    if (vdom.props) {
        Object.keys(vdom.props)
            .filter(key => key !== 'children')
            .forEach(name => renderedElement[name] = vdom.props[name]);
    }

    if (vdom.props.children) {
        vdom.props.children.forEach(child => typeof child === 'object' ? render(child, renderedElement) : container.appendChild(document.createTextNode(child)));
    }

    container.appendChild(renderedElement);
}


const App = () => (
    <div className="foo">
        <h1>Hello World</h1>
        <p>This is my first virtual DOM</p>
    </div>
);

window.onload = () => render(<App />, document.getElementById('app'));
